.navbar {
  background: linear-gradient(90deg, rgb(69, 129, 47) 0%, rgb(12, 87, 47)  100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  position: sticky;
  font-family: 'PT Sans', sans-serif;
  top: 0;
  z-index: 999;
  letter-spacing: 0.7px;

}

.navbar-logo {
  color: #fff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  padding-top: 0.8rem;
}

.fa-firstdraft {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
  border-color: #ef3e33;
}


.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-bottom: 4px solid transparent;
}

.nav-links_active {
  /* background-color: #C5CF2F;*/
  border-bottom: 4px solid #C5CF2F;
  border-radius: 0px;
  transition: all 0.2s ease-out;
}

.nav-links:hover {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-bottom: 4px solid #C5CF2F;
}


.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .navbar-logo {
    display: block;
    position: absolute;
    padding-top: 0.5rem;
    left: 6%;
  }

  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: block;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    display: block;
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    border-bottom: 3px solid transparent;
  }

  .nav-links:hover {
    background-color: #C5CF2F;
    padding: 2rem;
    color: #0C572F;
    border-radius: 0;
  }


  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    background: #C5CF2F;
    text-decoration: none;
    color: #fff;
    font-size: 1.5rem;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #C5CF2F;
    transition: 250ms;
  }

  button {
    display: none;
  }
}
