.cards {
  padding: 4rem;
  background: #fff;
  padding-top: 3rem;
}

.titulo-2 {
  text-align: center;
  color: #0C572F;
  font-size: 3rem;
  font-family: 'PT Sans', sans-serif;
  letter-spacing: 0.5px;
  margin-top: 10px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1500px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 0px;
  width: 300px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 0px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #252e48;
  font-size: 20px;
  line-height: 24px;
  font-family: 'PT Sans', sans-serif;
  text-align: center;
  letter-spacing: 0.5px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }

}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
    width: 300px;
  }
}
@media screen and (max-width: 960px) {

  .sede-container {
      display: row;
      width: 90%;
      margin-top: 15px;
      margin-bottom: 150px;
  }

  .titulo-2 {
    text-align: center;
    color: #0C572F;
    font-size: 2.5rem;
    font-family: 'PT Sans', sans-serif;
    letter-spacing: 0.5px;
  }
}